import { useRef } from "react";

export const LandingSection = () => {
    const refRef = useRef(null)
    const useLayoutEffect = () => {
        window.addEventListener('scroll', onScroll)
        return () => window.removeEventListener('scroll', onScroll)
    }

    const onScroll = () => {
        const scrollPos = window.scrollY + window.innerHeight
        document.documentElement.style.setProperty('--background-color-opacity', 1-((scrollPos-window.innerHeight)/window.innerHeight));
        if (scrollPos > 2*(window.innerHeight)) {
            document.documentElement.style.setProperty('--landing-section-positioning', "relative");
            document.documentElement.style.setProperty('--filler-height-1', "100vh");
            document.documentElement.style.setProperty('--filler-height-2', "0vh");
            document.documentElement.style.setProperty('--hue-rotation', "0deg")
        } else {
            document.documentElement.style.setProperty('--landing-section-positioning', "fixed");
            document.documentElement.style.setProperty('--filler-height-1', "0vh");
            document.documentElement.style.setProperty('--filler-height-2', "200vh");
            document.documentElement.style.setProperty('--hue-rotation', ((scrollPos-window.innerHeight)/(window.innerHeight/360))+"deg");
        }
    }
    
    useLayoutEffect(() => {})
    return (
        <div id="start">
            <div class="LandingSectionFiller1"/>
            <div ref={refRef} class="LandingSection">
                <h1 class="name">Marius Genton</h1>
            </div>
            <div class="LandingSectionFiller2"/>
        </div>
    )
}


