            //label: 'Perceived skill level',
            //data: [
            //    {name: "Swift", level: 80},
            //    {name: "Python", level: 75},
            //    {name: "C++", level: 0},
            //    {name: "HTML", level: 50},
            //    {name: "CSS", level: 40},
            //    {name: "JavaScript", level: 100},
            //    {name: "TypeScript", level: 60},
