import {ProjectsSection} from "./ProjectsSection.js";
import {LandingSection} from "./LandingSection.js"
import { EducationSection } from "./EducationSection.js";
import { ContactSection } from "./ContactSection.js";
import { Player } from '@lottiefiles/react-lottie-player';
import { StandardSection } from "./StandardSection.js";
import Bar from "./Bar.tsx"
import React from 'react';

import FRFlag from "./Flags/FR.png"
import NOFlag from "./Flags/NO.png"
import USFlag from "./Flags/US.png"
import SPFlag from "./Flags/SP.png"

class HamburgerMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {expanded: false};
    this.playerRef = React.createRef();
    this.toggle = this.toggle.bind(this);
    window.addEventListener('scroll', this.onScroll)
  }

  toggle() {
    this.setState({expanded: !this.state.expanded});
    if (this.state.expanded === false) {
      this.state.lottie.setDirection(1);
      this.state.lottie.play();
    } else {
      this.state.lottie.setDirection(-1);
      this.state.lottie.play();
    }
  }

  onScroll() {
    this.setState({expanded: false});
  }



  render() {
    return (
      <div class={this.state.expanded ? "MenuContainer expanded":"MenuContainer"}>
        <button class="burgerMenuButton" onClick={this.toggle}>
          <Player class="burgerMenu" lottieRef={instance => {
          this.setState({ lottie: instance }); // the lottie instance is returned in the argument of this prop. set it to your local state
        }} src='https://assets6.lottiefiles.com/packages/lf20_xwtrtoiq.json' className="player" ref={this.playerRef} keepLastFrame={true}/>
        </button>
        <div class="LinkContainer" hidden={!this.state.expanded}>
          <a href="#start" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Start</b></a>
          <a href="#projects" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Projects</b></a>
          <a href="#education" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Education</b></a>
          <a href="#skills" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Skills</b></a>
          <a href="#certifications" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Certifications</b></a>
          <a href="#languages" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Languages</b></a>
          <a href="#contact" class="page_references" hidden={!this.state.expanded} onClick={this.toggle}><b>Contact</b></a>
        </div>
      </div>
    );
  }
}


function App() {
  
  return (
    <div className="App">
      <HamburgerMenu/>
      <LandingSection/>
      <ProjectsSection/> 
      <EducationSection/>

      <StandardSection id="skills" height="40vh" title="TECHNICAL SKILLS">
        <Bar class="Bar"/>
      </StandardSection>
      
      <StandardSection title="CERTIFICATIONS" id="certifications" height="40vh" backgroundColor="#ecf0f1">
        <p>Duolingo English Test</p>
      </StandardSection>
      
      <StandardSection title="LANGUAGES" id="languages" height="35vh">
        <div style={{display: "grid", gridTemplateColumns: "1fr 1fr", gridTemplateRows: "1fr 1fr", height: "100%", width: "100%"}}>
          
          <div style={{display: "flex", width: "100%", height: "100%", alignItems: "center", textAlign: "left"}}>
            <img src={NOFlag} style={{width: "10%", objectFit: "contain"}} alt="Norwegian Flag"/>
            <div style={{marginLeft: "10px"}}>
              <h3>Norwegian</h3>
              <h4>Native proficiency</h4>
            </div>
          </div>

          <div style={{display: "flex", width: "100%", height: "100%", alignItems: "center", textAlign: "left"}}>
            <img src={FRFlag} style={{width: "10%", objectFit: "contain"}} alt="French Flag"/>
            <div style={{marginLeft: "10px"}}>
              <h3>French</h3>
              <h4>Native proficiency</h4>
            </div>
          </div>

          <div style={{display: "flex", width: "100%", height: "100%", alignItems: "center", textAlign: "left"}}>
            <img src={USFlag} style={{width: "10%", objectFit: "contain"}} alt="US Flag"/>
            <div style={{marginLeft: "10px"}}>
              <h3>English</h3>
              <h4>Full Professional proficiency</h4>
            </div>
          </div>

          <div style={{display: "flex", width: "100%", height: "100%", alignItems: "center", textAlign: "left"}}>
            <img src={SPFlag} style={{width: "10%", objectFit: "contain"}} alt="Spanish Flag"/>
            <div style={{marginLeft: "10px"}}>
              <h3>Spanish</h3>
              <h4>Professional proficiency</h4>
            </div>
          </div>
          
        </div>

      </StandardSection>

      <ContactSection/>
    </div>
  );
}

export default App;
